.portfolio-wrap:hover::before {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 1; }

.portfolio-wrap:hover .portfolio-info {
  opacity: 1; }

.portfolio-wrap:hover .portfolio-info::before {
